html, body { box-sizing: border-box; font-size: 20px; }

@media (max-width: 1200px) { html, body { font-size: calc(15.2px + 0.4vw) ; } }

*, *::before, *::after { box-sizing: inherit; }

:root { font-family: "Bellefair", serif; line-height: 1.6; }

:root * { -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; -webkit-overflow-scrolling: touch; }

a { color: inherit; transition: all 0.2s ease; }

a:hover { color: #ecbd00; }

.wrapper { width: 100%; font-family: "Bellefair", serif; color: #0a0a0a; line-height: 1.2; }

.wrapper, .wrapper #barba-wrapper, .wrapper .barba-container { display: flex; flex-direction: column; justify-content: space-between; min-height: 100.1vh; }

.c_yellow { color: #ecbd00; }

.main { flex-grow: 1; }

.header { padding: 0; background-color: #fff; }

.header .navbar { max-width: 1248px; margin: 0 auto; }

.header__logo .icon-main-logo { height: 4rem; }

@media only screen and (max-width: 992px) { .header .main-menu { border-top: 1px solid #fff; border-bottom: 1px solid #fff; } }

@media only screen and (min-width: 768px) { .header .main-menu .nav-item:not(:last-child) { margin-right: 1.5em; } }

.header .main-menu .nav-item:hover .nav-link { background-color: #fff; color: #ecbd00; }

.header .main-menu .nav-item .dropdown-toggle::after { display: none; }

.header .main-menu .nav-item.nav-item--current a { color: #ecbd00; }

.header .main-menu .nav-link { font-size: 1rem; padding: .2em 0.2em 0.2em; color: #0a0a0a; text-transform: uppercase; transition: all 0.3s ease; cursor: pointer; }

@media only screen and (max-width: 786px) { .header .main-menu .nav-link { text-align: center; padding: .5em 0.2em; } }

.header .main-menu .dropdown-menu { min-width: 9rem; padding: 1em 0 0 0; margin: 0; border: none; border-radius: 0; box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1), 0 8px 15px 0 rgba(0, 0, 0, 0.1); }

.header .main-menu .dropdown-item { color: #1bafff; padding: .25rem 0.6em; font-size: 1em; }

.header .main-menu .dropdown-item:hover { color: #1476d1; background: transparent; }

.header .main-menu .dropdown-item:active { color: #ffffff; background-color: #4ec1ff; }

.header .navbar-toggler { border: none; color: #fff; }

.header .navbar-toggler:focus { outline: none !important; }

.header .navbar-toggler-icon { width: 2rem; height: 2rem; background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#ecbd00' stroke-width='3' stroke-linecap='butt' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E"); }

.hero__bg-slide { display: block; width: 100%; min-height: 300px; max-height: 590px; object-fit: cover; object-position: center; }

.content { padding: 0.2rem 0 2.7rem; }

.content > .content__section { padding-top: 2rem; }

.content > .content__section:not(:last-child) .content__text { border-bottom: 1px solid #e9e9e9; }

.content__title { font-size: 1.8rem; text-transform: uppercase; }

.content__text { margin-left: 1rem; padding: 2.5rem 0 1rem; font-size: 1.2rem; }

@media only screen and (min-width: 480px) { .content__text { margin-left: 4rem; } }

.content__text p { margin-bottom: 1rem; }

.contact__mail { padding-bottom: 1rem; border-bottom: 1px solid #e9e9e9; }

.footer { padding: 2rem 0; background-color: #e4e4e4; }

.footer__copy { font-size: 1rem; line-height: 1.2; text-align: center; color: #707070; }

