@import "reset";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
@import "libs/slick";
@import "libs/slick-theme";
html,
body {
  box-sizing: border-box;
  @include rfs(20);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

a {
  color: inherit;
  transition: all 0.2s ease;
  &:hover {
    color: #ecbd00;
  }
}

.wrapper {
  width: 100%;
  font-family: $font-primary;
  color: #0a0a0a;
  line-height: 1.2;
  &,
  #barba-wrapper,
  .barba-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100.1vh;
  }
}

.c_yellow {
  color: #ecbd00;
}

.main {
  flex-grow: 1;
}

// HEADER
.header {
  padding: 0;
  background-color: #fff;
  .navbar {
    max-width: 1248px;
    margin: 0 auto;
  }
  &__logo {
    .icon-main-logo {
      height: 4rem;
    }
  }
  .main-menu {
    @media only screen and (max-width: 992px) {
      border-top: 1px solid #fff;
      border-bottom: 1px solid #fff;
    }
    .nav-item {
      &:not(:last-child) {
        @media only screen and (min-width: 768px) {
          margin-right: 1.5em;
        }
      }
      &:hover {
        .nav-link {
          background-color: #fff;
          color: #ecbd00;
        }
      }
      .dropdown-toggle::after {
        display: none;
      }
      &.nav-item--current a {
        color: #ecbd00;
      }
    }
    .nav-link {
      font-size: 1rem;
      padding: .2em 0.2em 0.2em;
      color: #0a0a0a;
      text-transform: uppercase;
      transition: all 0.3s ease;
      cursor: pointer;
      @media only screen and (max-width: 786px) {
        text-align: center;
        padding: .5em 0.2em;
      }
    }
    .dropdown-menu {
      min-width: 9rem;
      padding: 1em 0 0 0;
      margin: 0;
      border: none;
      border-radius: 0;
      box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1), 0 8px 15px 0 rgba(0, 0, 0, 0.1);
    }
    .dropdown-item {
      color: #1bafff;
      padding: .25rem 0.6em;
      font-size: 1em;
      &:hover {
        color: #1476d1;
        background: transparent;
      }
      &:active {
        color: #ffffff;
        background-color: lighten(#1bafff, 10%);
      }
    }
  }
  .navbar-toggler {
    border: none;
    color: #fff;
    &:focus {
      outline: none!important;
    }
  }
  .navbar-toggler-icon {
    width: 2rem;
    height: 2rem;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#ecbd00' stroke-width='3' stroke-linecap='butt' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
  }
}

.hero__bg-slide {
  display: block;
  width: 100%;
  min-height: 300px;
  max-height: 590px;
  object-fit: cover;
  object-position: center;
}

// Content sections
.content {
  padding: 0.2rem 0 2.7rem;
  &>.content__section {
    padding-top: 2rem;
    &:not(:last-child) {
      .content__text {
        border-bottom: 1px solid #e9e9e9;
      }
    }
  }
  &__title {
    font-size: 1.8rem;
    text-transform: uppercase;
  }
  &__text {
    margin-left: 1rem;
    @media only screen and (min-width: 480px) {
      margin-left: 4rem;
    }
    padding: 2.5rem 0 1rem;
    font-size: 1.2rem;
    p {
      margin-bottom: 1rem;
    }
  }
}

.contact__mail {
  padding-bottom: 1rem;
  border-bottom: 1px solid #e9e9e9;
}

// Footer
.footer {
  padding: 2rem 0;
  background-color: #e4e4e4;
  &__copy {
    font-size: 1rem;
    line-height: 1.2;
    text-align: center;
    color: #707070;
  }
}

@import "media";